@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App:first-child {
  padding-top: 2rem;
}

body {
  font-family: 'Cabin Sketch', sans-serif !important;
}


.form-control {
    width: 30rem;
    color: white;
    background: transparent;
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-bottom-style: solid;
    border-color: white;
}
.form-control::-webkit-input-placeholder {
    color: rgb(150, 150, 150);
}
.form-control:-ms-input-placeholder {
    color: rgb(150, 150, 150);
}
.form-control::-ms-input-placeholder {
    color: rgb(150, 150, 150);
}
.form-control::placeholder {
    color: rgb(150, 150, 150);
}
.form-control:focus {
    background: transparent;
    color: white;
    background: transparent;
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-bottom-style: solid;
    border-color: white;
}
.example {
    width: 100vw;
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
    font-size: 15px;
    padding-top: .5rem;
    padding-bottom: .5rem;
}
hr {
    border: .5px solid rgb(23, 162, 184);
}
.colored {
    color: rgb(93, 150, 160);
}
.errorMsg {
    color: #f7dd72;
    font-size: 14px;
}




.ps {
    font-size: 12px;
    margin: none;
}
.body {
    padding: none;
    font-size: 20px;
}
.colored {
    color: rgb(93, 150, 160);
}
.ss {
    text-decoration: underline;
    -webkit-text-decoration-color: rgb(93, 150, 160);
            text-decoration-color: rgb(93, 150, 160);
}

.card {
    background-color: transparent;
    margin-top: 10px;
    font-size: 14px;
    border-radius: 10px;
    border-color: rgb(23, 162, 184);   
    max-width: 300px;
    -webkit-animation-name: animatetop;
            animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s
}

.card-footer {
    background-color: rgb(93, 150, 160);
    
}
.card-footer:last-child {
    border-radius: 5px;
}

.icon {
    cursor: pointer;
    font-size: 12px;
    color: white;
    transition:         transform .2s ease-in-out;
}
.icon:hover, .icon:focus {
    transform: rotate(90deg);
}
hr {
    max-width: 500px;
}

.item-details {
    display: flex;
    margin-top: 1rem;
    flex-direction: row;
    padding: 1rem; 
    -webkit-animation-name: animatetop; 
            animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s
}
/* Add Animation */
@-webkit-keyframes animatetop {
    from {left: -300px; opacity: 0}
    to {left: 0; opacity: 1}
}
@keyframes animatetop {
    from {left: -300px; opacity: 0}
    to {left: 0; opacity: 1}
}



  

  





